h2 {
    font-weight: border;
}

.hr1 {
    border: 0;
    height: 2px;
    background: #d3d3d3;
}

.grey {
    color: #727272;
}

#strong {
    font-weight: 900;
}

table {
    width: 100%;
    border-top: 1px solid #d3d3d3;
    border-collapse: collapse;
}

th {
    background-color: #d3d3d3;
    border-top: 2px solid #727272;
}

th, td {
    border-bottom: 1px solid #d3d3d3;
    /*border-right: 1px solid #d3d3d3;*/
    padding: 10px;
}

.greylist {
    width: 50px;
    height: 30px;
    font-weight: 900;
    color: white;
    text-align: center;
    background: grey;
    border: solid 2px white;
    border-radius: 5px;
}

.gradient {
    width: 80px;
    height: 30px;
    font-weight: 900;
    color: white;
    text-align: center;
    background: linear-gradient(to bottom, grey, black);
    border: solid 2px white;
    border-radius: 5px;
}

.left {
    text-align: left;
}

.right {
    float: right;
}

.center {
    text-align: center;
}

a {
    color: black;
    text-decoration-line: none;
}
.small-col {
    width: 5%;
}

.sl-col{
    width: 10%;
}

.middle-col{
    width: 20%;
}

.large-col {
    width: 40%;
}